* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.input-background {
  height: 60px;
  background-color: #7fff00;
  align-items: center;
  justify-items: center;
  margin: 0;
  display: grid;
}

.search-form {
  display: flex;
}

.search-input {
  height: 40px;
  width: 400px;
  border: 0;
  border-radius: 6px 0 0 6px;
  padding-left: 8px;
}

.input-button {
  height: 40px;
  width: 40px;
  border: 0;
  border-radius: 0 6px 6px 0;
}

.search-icon {
  height: 20px;
  vertical-align: middle;
}

.search-input:focus {
  outline: none;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 15px;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.photo-card {
  width: 350px;
  align-content: center;
  display: grid;
}

.gallery img {
  object-fit: cover;
  height: 200px;
  width: 350px;
}

.info-item {
  flex-direction: column;
  margin: 10px auto;
  display: flex;
}

.info {
  text-align: center;
  border: 1px solid gray;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  display: flex;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.load-more {
  width: 180px;
  height: 40px;
  color: #000;
  background-color: #7fff00;
  border: none;
  border-radius: 10px;
  margin: 0 auto 20px;
  font-size: 20px;
  display: block;
}

.hidden {
  display: none;
}

